.detail {
  height: 100%;
  background-color: rgb(235, 235, 235);
}

.my-drawer {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.my-drawer .am-drawer-sidebar {
  background-color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.my-drawer .am-drawer-sidebar .am-list {
  width: 4.8rem;
  padding: 0;
}

.my-accordion {
  overflow-y: scroll;
}

.am-list-item .am-list-line .am-list-extra {
  flex-basis: auto;
}

.clear-confirm {
  text-align: center;
  margin: 0.2rem auto;
}

.clear-confirm a {
  margin: 0 0.2rem;
}
