body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

#root {
  height: 100%;
}

#root .am-list-item .am-input-extra {
  max-height: 0.6rem;
}
#root .ant-table-thead tr th {
  text-align: center !important;
}


@font-face {font-family: "iconfont";
  src: url(../../static/media/iconfont.63c22058.eot); /* IE9*/
  src: url(../../static/media/iconfont.63c22058.eot#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
  url(../../static/media/iconfont.e6a479a2.ttf) format('truetype'), 
  url(../../static/media/iconfont.0f350c05.svg#iconfont) format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-share:before { content: "\E659"; }

.icon-read:before { content: "\E624"; }

.icon-pic:before { content: "\E688"; }

.icon-key:before { content: "\E648"; }

.icon-profile:before { content: "\E608"; }

.icon-shijian:before { content: "\E63D"; }

.icon-hao:before { content: "\E66E"; }

.icon-Fill:before { content: "\E503"; }

.icon-file:before { content: "\E953"; }

.icon-lock:before { content: "\E6A4"; }


.login .logo {
  text-align: center;
  font-size: 0.5rem;
  font-weight: 500;
  height: 2.5rem;
  line-height: 2.5rem;
}

.login .tip {
  font-size: 0.2rem;
  text-align: center;
  margin: 0.1rem auto;
}

.login .main-color {
  color: rgb(92, 156, 248);
}

.iconfont.icon {
  font-size: 0.3rem;
}

.am-list-item .am-input-label.am-input-label-5 {
  width: 0.4rem;
}
.detail {
  height: 100%;
  background-color: rgb(235, 235, 235);
}

.my-drawer {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.my-drawer .am-drawer-sidebar {
  background-color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.my-drawer .am-drawer-sidebar .am-list {
  width: 4.8rem;
  padding: 0;
}

.my-accordion {
  overflow-y: scroll;
}

.am-list-item .am-list-line .am-list-extra {
  flex-basis: auto;
}

.clear-confirm {
  text-align: center;
  margin: 0.2rem auto;
}

.clear-confirm a {
  margin: 0 0.2rem;
}

